'use client'

import { useState, useEffect, useCallback } from 'react'
import { Wallet, ChevronDown, Copy, ExternalLink, LogOut, Check, AlertCircle, Loader2 } from 'lucide-react'
import { cn } from '@/lib/utils'
import { Button } from '@/components/ui/button'
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu'
import { formatKAS } from '@/lib/mock-data'
import type { WalletState } from '@/types/auction'

// Define KasWare window interface
declare global {
  interface Window {
    kasware: any;
  }
}

export function WalletButton() {
  const [wallet, setWallet] = useState<WalletState>({
    connected: false,
  })
  const [copied, setCopied] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [isDetected, setIsDetected] = useState(false)
  const [isConnecting, setIsConnecting] = useState(false)

  const updateWalletInfo = useCallback(async (address: string) => {
    try {
      if (!window.kasware) {
        console.error('[WalletButton] KasWare not found in updateWalletInfo');
        return;
      }

      console.log('[WalletButton] Updating wallet info for:', address);

      const [balanceObj, allAccounts] = await Promise.all([
        window.kasware.getBalance(),
        window.kasware.getAccounts()
      ]);

      console.log('[WalletButton] Balance:', balanceObj, 'All Accounts:', allAccounts);

      setWallet({
        connected: true,
        address,
        allAccounts: allAccounts || [address],
        balance: (balanceObj?.total || 0) / 1e8, // Convert Sompi to KAS
        name: 'KasWare Wallet',
      })
    } catch (err) {
      console.error('[WalletButton] Failed to update wallet info:', err)
      setError('Failed to fetch balance')
    }
  }, [])

  useEffect(() => {
    let checkCount = 0;
    const maxChecks = 10;

    const checkDetection = async () => {
      if (typeof window.kasware !== 'undefined') {
        const network = await window.kasware.getNetwork();
        console.log('[WalletButton] KasWare detected! Network:', network);
        setIsDetected(true);

        try {
          const accounts = await window.kasware.getAccounts()
          console.log('[WalletButton] Current accounts:', accounts);
          if (accounts && accounts.length > 0) {
            updateWalletInfo(accounts[0])
          }
        } catch (err) {
          console.warn('[WalletButton] Initial account check failed:', err);
        }

        window.kasware.on('accountsChanged', (accounts: string[]) => {
          console.log('[WalletButton] Accounts changed event:', accounts);
          if (accounts && accounts.length > 0) {
            updateWalletInfo(accounts[0])
          } else {
            console.log('[WalletButton] No accounts, setting disconnected');
            setWallet({ connected: false })
          }
        })

        window.kasware.on('networkChanged', (network: string) => {
          console.log('[WalletButton] Network changed event:', network);
          window.location.reload()
        })
        return true;
      }
      return false;
    }

    const interval = setInterval(async () => {
      const detected = await checkDetection();
      checkCount++;
      if (detected || checkCount >= maxChecks) {
        clearInterval(interval);
      }
    }, 500);

    checkDetection();

    return () => clearInterval(interval);
  }, [updateWalletInfo])

  const handleConnect = async () => {
    // 1. Check for provider
    let provider = typeof window !== 'undefined' ? (window as any).kasware : null;

    // 2. If not found, try a quick 100ms delay and check again
    if (!provider) {
      await new Promise(resolve => setTimeout(resolve, 200));
      provider = typeof window !== 'undefined' ? (window as any).kasware : null;
    }

    console.log('[WalletButton] handleConnect triggered. Provider found:', !!provider);

    if (!provider) {
      console.warn('[WalletButton] KasWare provider not found at click time');
      setError('KasWare Wallet not detected. Please ensure it is enabled and refresh the page.');
      setIsDetected(false); // Reset detection state
      return
    }

    try {
      setIsConnecting(true)
      setError(null)
      setIsDetected(true) // Ensure it's marked as detected if we found the provider

      console.log('[WalletButton] Requesting accounts...');
      const accounts = await provider.requestAccounts()
      console.log('[WalletButton] Request accounts result:', accounts);

      if (accounts && accounts.length > 0) {
        await updateWalletInfo(accounts[0])
      } else {
        throw new Error('Connect request cancelled or no accounts found')
      }
    } catch (err: any) {
      console.error('[WalletButton] Connection error:', err);
      setError(err.message || 'Failed to connect')
      setTimeout(() => setError(null), 5000)
    } finally {
      setIsConnecting(false)
    }
  }

  const handleDisconnect = () => {
    // KasWare doesn't have a direct disconnect, so we just clear local state
    setWallet({ connected: false })
  }

  const handleCopyAddress = () => {
    if (wallet.address) {
      navigator.clipboard.writeText(wallet.address)
      setCopied(true)
      setTimeout(() => setCopied(false), 2000)
    }
  }

  if (!wallet.connected) {
    return (
      <div className="flex flex-col items-end gap-2">
        <Button
          onClick={handleConnect}
          disabled={isConnecting}
          className={cn(
            "gap-2 bg-[#4F46E5] hover:bg-[#4338CA] text-white font-medium shadow-kaspa transition-all hover:shadow-kaspa-lg disabled:opacity-70",
            isConnecting && "animate-pulse"
          )}
        >
          {isConnecting ? (
            <Loader2 className="h-4 w-4 animate-spin" />
          ) : (
            <Wallet className="h-4 w-4" />
          )}
          <span className="hidden sm:inline text-sm">
            {isConnecting ? 'Connecting...' : (isDetected ? 'Connect Wallet' : 'Retry Connection')}
          </span>
          <span className="sm:hidden">{isConnecting ? '...' : (isDetected ? 'Connect' : 'Retry')}</span>
        </Button>

        {error && (
          <div className="flex flex-col items-end gap-1 px-1">
            <div className="flex items-center gap-1 text-[11px] font-medium text-red-500 animate-in fade-in slide-in-from-top-1">
              <AlertCircle className="h-3 w-3" />
              <span>{error}</span>
            </div>
            {!isDetected && !isConnecting && (
              <a
                href="https://chromewebstore.google.com/detail/kasware-wallet/hklhheigdmpoolooomdihmhlpjjdbklf"
                target="_blank"
                rel="noopener noreferrer"
                className="text-[10px] text-[#4F46E5] underline hover:text-[#4338CA]"
              >
                Missing wallet? Install here
              </a>
            )}
          </div>
        )}
      </div>
    )
  }

  const truncatedAddress = wallet.address
    ? `${wallet.address.slice(0, 10)}...${wallet.address.slice(-4)}`
    : ''

  return (
    <DropdownMenu>
      <DropdownMenuTrigger asChild>
        <Button
          variant="outline"
          className="gap-2 border-[#4F46E5]/30 bg-[#4F46E5]/5 hover:bg-[#4F46E5]/10 animate-pulse-glow"
        >
          <div className="flex items-center gap-2">
            <div className="flex h-6 w-6 items-center justify-center rounded-full bg-[#4F46E5]">
              <Wallet className="h-3 w-3 text-white" />
            </div>
            <div className="hidden flex-col items-start sm:flex">
              <span className="text-xs font-semibold text-[#4F46E5] tabular-nums">
                KAS: {formatKAS(wallet.balance || 0)}
              </span>
            </div>
            <span className="text-xs font-semibold text-[#4F46E5] tabular-nums sm:hidden">
              {formatKAS(wallet.balance || 0)}
            </span>
          </div>
          <ChevronDown className="h-3 w-3 text-muted-foreground" />
        </Button>
      </DropdownMenuTrigger>
      <DropdownMenuContent align="end" className="w-64">
        <div className="p-3">
          <p className="text-sm font-medium text-foreground">{wallet.name}</p>
          <p className="mt-1 text-xs text-muted-foreground font-mono">
            {truncatedAddress}
          </p>
        </div>
        <DropdownMenuSeparator />
        <div className="p-3">
          <p className="text-xs text-muted-foreground">Balance</p>
          <p className="text-lg font-bold text-foreground tabular-nums">
            {formatKAS(wallet.balance || 0)} <span className="text-sm font-medium text-muted-foreground">KAS</span>
          </p>
        </div>
        <DropdownMenuSeparator />
        <div className="p-2">
          <p className="px-2 pb-1 text-[10px] font-medium uppercase tracking-wider text-muted-foreground">
            Connect Accounts
          </p>
          <div className="max-h-32 overflow-y-auto space-y-1">
            {wallet.allAccounts?.map((account) => (
              <DropdownMenuItem
                key={account}
                onClick={() => updateWalletInfo(account)}
                className={cn(
                  "flex flex-col items-start px-2 py-1.5 cursor-pointer rounded-md",
                  wallet.address === account ? "bg-[#4F46E5]/10 border-[#4F46E5]/20" : "hover:bg-accent"
                )}
              >
                <div className="flex w-full items-center justify-between">
                  <span className="text-[11px] font-mono truncate">
                    {account.slice(0, 8)}...{account.slice(-4)}
                  </span>
                  {wallet.address === account && (
                    <Check className="h-3 w-3 text-[#4F46E5]" />
                  )}
                </div>
              </DropdownMenuItem>
            ))}
          </div>
        </div>
        <DropdownMenuSeparator />
        <DropdownMenuItem onClick={handleCopyAddress} className="cursor-pointer">
          {copied ? (
            <Check className="mr-2 h-4 w-4 text-emerald-500" />
          ) : (
            <Copy className="mr-2 h-4 w-4" />
          )}
          {copied ? 'Copied!' : 'Copy Address'}
        </DropdownMenuItem>
        <DropdownMenuItem asChild className="cursor-pointer">
          <a
            href={`https://explorer.kaspa.org/addresses/${wallet.address}`}
            target="_blank"
            rel="noopener noreferrer"
          >
            <ExternalLink className="mr-2 h-4 w-4" />
            View on Explorer
          </a>
        </DropdownMenuItem>
        <DropdownMenuSeparator />
        <DropdownMenuItem
          onClick={handleDisconnect}
          className="cursor-pointer text-destructive focus:text-destructive"
        >
          <LogOut className="mr-2 h-4 w-4" />
          Disconnect
        </DropdownMenuItem>
      </DropdownMenuContent>
    </DropdownMenu>
  )
}
